section#staff {
  padding-top: 90px;
  padding-bottom: 112px;
  text-align: center;
  .icon {
    background: url("../img/vector/staff.svg");
    width: 95px;
    height: 69px;
    display: inline-block;
    margin-bottom: 50px; }
  .staff-list {
    text-align: center;
    .group-title {
      margin-bottom: 40px; }
    .title {
      color: #6f7172;
      font-size: 18px;
      text-align: center;
      @include clearfix; }
    .title.last {
      font-size: 16px; }
    .person {
      margin: 0 15px;
      display: inline-block;
      margin-bottom: 50px;
      text-align: center;
      img {
        border-radius: 50%; }
      .name {
        margin-top: 24px;
        color: #6f7172;
        font-size: 16px; }
      .position {
        color: #6f7172;
        font-size: 14px;
        font-weight: 700; } } } }
