#sidebar {
  padding-top: 63px;
  width: 274px;
  background: url("../img/external/sidebar.png") no-repeat;
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      display: block;
      a {
        color: #3d3d3d;
        font-family: OpenSans;
        font-size: 15px;
        font-weight: 400;
        line-height: 20px;
        &:hover {
          text-decoration: underline; } } }
    li.active {
        a {
          color: #29a2ff;
          font-weight: 700; } }
    .sub-menu {
      margin-left: 20px;
      li {
        a {
          font-size: 12px; } } } }
  h2 {
    float: left;
    font-weight: 700;
    font-size: 18px; } }
