section#rules {
  text-align: center;
  height: 820px;
  padding: 90px 0 100px 0;
  .icon {
    display: inline-block;
    background: url("../img/vector/rules.svg");
    width: 74px;
    height: 72px; }
  .document-list {
    margin-top: 70px;
    text-align: left;
    .row {
      border-bottom: 1px solid #dadada;
      padding: 40px 0;
      &:first-child {
        border-top: 1px solid #dadada; }
      .title {
        color: #6f7172;
        font-size: 18px;
        margin-bottom: 10px; }
      a {
        display: inline-block;
        width: 22%;
        vertical-align: middle;
        position: relative;
        padding-left: 30px;
        &:before {
          position: absolute;
          top: 0;
          left: 0px;
          right: 0;
          bottom: 0;
          margin: auto 0;
          display: block;
          width: 23px;
          height: 30px;
          background: url("../img/vector/link.svg") no-repeat;
          content: ""; } } } } }
