@mixin logo {
  background: {
    image: url('../img/vector/raicom-logo.svg'); }
  @include transition(); }
@mixin button {
  background: none;
  color: white;
  font-size: 12px;
  font-weight: 600;
  border: 2px solid white;
  padding: 9px 35px;
  box-sizing: border-box;
  @include transition();
  &:hover {
    opacity: 0.8; } }
@mixin sub-title {
  border-left: 3px solid #29a2ff;
  padding-left: 27px; }
